import React, { Component } from 'react';
import SocketContext from '../context/socket-context';

class LandingPage extends Component {
  constructor(props){
    super(props);
    this.state = {
      sessionName: '',
      socketId : ''
    }
  }

  componentWillMount(){
    let socket = this.context.socket;
    socket.on('connect', () => {
      console.log('Socket connected', socket.id);
      this.context.socketId = socket.id;
      this.setState({
        socketId: socket.id
      })
    });
  }

  handleInput = (event) => {
    this.setState({
      sessionName : event.target.value    
    })    
  }

  createSession(){
    let socket = this.context.socket;
    const {sessionName, socketId} = this.state;

    this.context.sessionName = sessionName;
    socket.emit('create-session', sessionName);
    this.props.history.push('/session/'+ btoa(sessionName));
  }

  render() {
    return ( 
      <SocketContext.Consumer>
        {
          (context) => {
            return(
              <div className="container wrapper">
                <h3>Backlog Grooming</h3>
                <p>Welcome to online backlog grooming app, it is time to invite members and start estimating stories and features. Invite your team to join session by sharing the generated URL.</p>
                {/* <p>Send this link to your team members to join the session.</p> */}
                <h3>Create a session</h3>
                <div className="card">
                  <div className="card-body">
                    <div className="form-group">
                      <label>Session name:</label>
                      <input type="text" className="form-control" onChange={this.handleInput}/>
                      <small id="emailHelp" className="form-text text-muted">Type any text as session name (ex. sprint-64)</small>
                    </div>
                    <button type="submit" className="btn btn-primary m-bottom-15" onClick={() => this.createSession()}>Create</button>
                  </div>
                </div>
              </div>
            )
          }
        }
      </SocketContext.Consumer>
      
    );
  }
}

LandingPage.contextType = SocketContext;
export default LandingPage;