import React, { Component } from 'react';
import ReactCardFlip from 'react-card-flip';
import SocketContext from '../context/socket-context';

class SessionPage extends Component {
  constructor(props){
    super(props);
    this.state = {
      story : {
        name: '',
        description: '',
        canVote : true
      },
      canReveal: false,
      userList : [],
      sessionName : ''
    }
  }

  componentDidMount(){
    let socket = this.context.socket;
    if(this.props.match.params && !this.props.match.params.sessionName) return
    let sessionName = this.props.match.params.sessionName;
    
    this.setState({
      sessionName : atob(sessionName)
    })

    socket.on('new-user-joined', (response) => {
      this.setState({
        userList : [...this.state.userList, response]
      })
    });

    socket.on('story-point', (response) => {
      let index = this.state.userList.findIndex(user => user.username == response.username);
      this.state.userList[index] = response;
      this.setState({
        userList : this.state.userList
      })
    });

  }

  handleInput = (event) => {
    this.setState({
      story : {...this.state.story, [event.target.name] : event.target.value}
    })
  }

  createStory(){
    let {sessionName} = this.state;
    let socket = this.context.socket;

    let {name} = this.state.story;
    if(name && name.trim() !== ''){
      this.state.userList = this.state.userList.map((user) => {
        return {
          username : user.username,
          status : false, point: false
        }
      })
      this.setState({
        canReveal : false,
        userList: this.state.userList
      });
      socket.emit('start-story-voting',sessionName, this.state.story);
    }
    else{
      alert('Please Enter a Valid Story Name')
    }
  }

  clearStory(){
    let {sessionName} = this.state;
    let socket = this.context.socket;
    let story =  {
      name: '',
      description: ''
    }
    this.state.userList = this.state.userList.map((user) => {
      return {
        username : user.username,
        status : false, point: false
      }
    })
    this.setState({
      canReveal : false,
      userList: this.state.userList,
      story : {...this.state.story, name: '', description: '', canVote : true}
    });
    socket.emit('start-story-voting',sessionName, story);
  }

  revealResult(){
    let {sessionName} = this.state;
    let socket = this.context.socket;
    socket.emit('story-points-revealed',sessionName);
    this.setState({
      canReveal : true
    });
  }

  removeUser(user){
    let index = this.state.userList.findIndex(u => u.username == user.username);
    this.state.userList.splice(index, 1);
    this.setState({
      userList: this.state.userList
    })
  }

  render() {
    let {name, description} = this.state.story;
    let userList = this.state.userList.map((user, index) => {
      return(
        <div className="col-lg-2 col-md-3 col-xs-4 m-bottom" key={index}>
            <div className="cardbox-container">
              <div className="cardbox selectable card-background-color">
                <ReactCardFlip isFlipped={this.state.canReveal}>
                  {user.status && 
                  <div className="inner">
                    <span className="cardbox-label">?</span>
                  </div>
                  }
                  {user.point &&<div className="inner">
                    <span className="cardbox-label">{user.point}</span>
                  </div>
                  }
                </ReactCardFlip>
                {/* { this.state.canReveal && user.point &&
                  <div className="inner">
                    <span className="cardbox-label">{user.point}</span>
                  </div>
                }
                { user.status && !this.state.canReveal &&
                  <div className="inner">
                    <span className="cardbox-label">?</span>
                  </div>
                } */}
              </div>
              
              <div className="delete-member remove" onClick={() => this.removeUser(user)}>
                <span className="glyphicon glyphicon-remove">X</span>
              </div>
            </div>
          <h5 className="userName">{user.username}</h5>
        </div>
      )
    })
    
    return (
      <SocketContext.Consumer>
        {
          (context) => {
            return (
              <div className="container m-top-1">
                <div>
                  <h3>{this.state.sessionName} - Session</h3>
                </div>
                <div className="card">
                  <div className="card-body">
                    <div className="form-group row">
                      <label className="col-sm-1 col-form-label">Story:</label>
                      <div className="col-sm-11">
                        <input type="text" name="name" value={name} className="form-control" onChange={this.handleInput}/>
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-sm-2 col-form-label">Description:</label>
                      <div className="col-sm-10">
                        <input type="text" name="description" value={description} className="form-control" onChange={this.handleInput}/>
                      </div>
                    </div>
                    <div className="form-group">
                      <button type="submit" className="btn btn-primary btn-sm btn-pad" onClick={() => this.createStory()}>Start</button>
                      <button type="submit" className="btn btn-info btn-sm space" onClick={() => this.revealResult()}>Reveal</button>
                      <button type="submit" className="btn btn-danger btn-sm space" onClick={() => this.clearStory()}>Clear</button>
                    </div>
                  </div>
                </div>
                <div className="row m-top-2">
                  {userList}
                </div>
                <div>
                  <h3>Invite members </h3>
                  <p>Invite members to join your session by sharing them this link</p>
                  <a href={context.domainName + 'join/' +btoa(this.state.sessionName)}>{context.domainName}join/{btoa(this.state.sessionName)}</a>
                  {/* <span className="link">{context.domainName}join/{btoa(this.state.sessionName)}</span> */}
                </div>
              </div>
            )
          }
        }
      </SocketContext.Consumer>
      
    );
  }
}

SessionPage.contextType = SocketContext;
export default SessionPage;