import React, { Component } from 'react';
import {HashRouter, Switch, Route} from 'react-router-dom';
import io from 'socket.io-client';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

import LandingPage from './pages/landing';
import SessionPage from './pages/session';
import JoinSessionPage from './pages/join-session';
import MemberPage from './pages/member';

import SocketContext from './context/socket-context';

const logo = require('./images/logo.png');
const socketConnection = io("https://scrumpocker.herokuapp.com/",{transports: ['websocket']});
// const socketConnection = io("http://localhost:3000/",{transports: ['websocket']});

window.onbeforeunload = function() {
  return "Data will be lost if you leave the page, are you sure?";
};

class App extends Component {
  render() {
    return (
      <SocketContext.Provider value={{
        socket : socketConnection,
        socketId: '',
        sessionName: '',
        // domainName : 'http://localhost:8000/#/'
        //domainName : 'https://concord-india.github.io/scrumpocker/#/'
        domainName : 'https://concord-india.github.io/backlog-grooming/#/'
      }}>
        <HashRouter>
        <nav className="navbar navbar-dark bg-blue">
          <a className="navbar-brand">
            <img src={logo.default} className="d-inline-block align-top logo" alt="concord" loading="lazy" />
          </a>
        </nav>
          <Switch>
            <Route>
              <Route path="/" exact component={LandingPage}></Route>
              <Route path="/session/:sessionName" component={SessionPage}></Route>
              <Route path="/join/:sessionName" component={JoinSessionPage}></Route>
              <Route path="/member/:username" component={MemberPage}></Route>
            </Route>
          </Switch>
        </HashRouter>
      </SocketContext.Provider>
    );
  }
}

export default App;