import React, { Component } from 'react';
import axios from 'axios';
import SocketContext from '../context/socket-context';

class MemberPage extends Component {
  constructor(props){
    super(props);
    this.state = {
      username: '',
      selectedPoint: '',
      storyPoints : [1, 2, 3 ,5, 8, 10, 13, 20, 40],
      story : {
        name: '',
        description: '',
        canVote : false
      },
      sessionName: '',
      // baseURL : 'http://localhost:3000',
      baseURL : 'https://scrumpocker.herokuapp.com',
      disable : false
    } 
  }

  componentDidMount(){
    let socket = this.context.socket;
    let routeData = this.props.history.location.state;
    if(!routeData) return;
    
    const url = this.state.baseURL + "/voting-status";
    this.setState({
      sessionName : routeData.sessionName,
      username : routeData.username,
    })
    socket.on('enable-story-voting', (response) => {
      this.setState({
        story : response,
        selectedPoint : '',
        disable : false
      })
    });
    
    socket.on('disable-users', (status) => {
      this.setState({
        disable : status
      })
    });

    axios.post(url, {sessionName : routeData.sessionName})
         .then((response) => {
            const {data} = response;
            if(data.canVote && data.story){
              this.setState({
                story : data.story,
                disable : false
              })
            }
         })
         .catch((error) => {
            alert(error);
         });
  }

  selectedCardbox(point){
    let socket = this.context.socket;
    let storyPoint = {
      username : this.state.username,
      point: point,
      status:true
    }
    this.setState({
      selectedPoint : point
    });
    
    socket.emit('user-story-point-selection', this.state.sessionName ,storyPoint);
  }


  render() {
    let storyPoint = this.state.storyPoints.map((point, index) => {
      return(
        <div className="col-lg-2 col-md-3 col-xs-4 m-bottom" key={index}>
            <div className="cardbox-container">
              <div className={point === this.state.selectedPoint ? "cardbox selectable cardConfirmed" : "cardbox selectable cardIsHover"}
                onClick={() => this.selectedCardbox(point)}>
                <div className="inner">
                  <span className="cardbox-label">{point}</span>
                </div>
              </div>
            </div>
          </div>
      )
    })
    return (
      <SocketContext.Consumer>
        {
          () => {
            let {name, description, canVote} = this.state.story;
            return(
              <div className="container">
                <h3>Welcome, {this.state.username}</h3>
                {canVote ? 
                <div>
                  <div className="card card-space">
                    
                    <div className="card-body">
                      <div className="form-group">
                        <h3>{name}</h3>
                      </div>
                      <div className="form-group">
                        <span>{description}</span>
                      </div>
                    </div>
                  </div>
                  {/* List of Cards */}
                  <div className={this.state.disable ? 'row disable-card' : 'row'}>
                    {storyPoint}
                  </div>
                </div> 
                : 
                <div className="wrapper">
                  <h3>Please wait for the scrum master to start the voting for story.</h3>
                </div>
              }
              </div>
            )
          }
        }
      </SocketContext.Consumer>
      
    );
  }
}

MemberPage.contextType = SocketContext;
export default MemberPage;