import React, { Component } from 'react';
import SocketContext from '../context/socket-context';

class JoinSessionPage extends Component {
  constructor(props){
    super(props);
    this.state = {
      username : ''
    }
  }
  
  handleInput = (event) => {
    this.setState({
      username : event.target.value    
    })    
  }

  joinSession(){
    let {username} = this.state;
    let socket = this.context.socket;
    if(this.props.match.params && !this.props.match.params.sessionName) return

    let sessionName = atob(this.props.match.params.sessionName);
    

    if(username && username.trim() !== ''){
      let newUser = {
        username, status : false, point: false, sessionName 
      }
      socket.emit('join-session', sessionName, newUser);
      this.props.history.push({
        pathname : '/member/'+ btoa(username), 
        state : newUser
      });
    }
    else{
      alert('Plesae Enter a Valid UserName')
    }
  }

  render() {
    return (
      <SocketContext.Consumer>
        {
          () => {
            return(
              <div className="container wrapper">
                <h3>Backlog Grooming</h3>
                <p>Welcome to online backlog grooming web app, it is time to invite members and start estimating stories and features.</p>
                <h3>Join session</h3>
                <div className="card">
                  <div className="card-body">
                    <div className="form-group">
                      <label>Name:</label>
                      <input type="text" className="form-control" onChange={this.handleInput}/>
                      <small id="emailHelp" className="form-text text-muted">Type name to join session (ex. John)</small>
                    </div>
                    <button type="submit" className="btn btn-primary m-bottom-15" onClick={() => this.joinSession()}>Join</button>
                  </div>
                </div>
              </div>
            )
          }
        }
      </SocketContext.Consumer>
    );
  }
}

JoinSessionPage.contextType = SocketContext;
export default JoinSessionPage;